import axios from 'axios';

const BACKEND_URL = process.env.NEXT_PUBLIC_BACKEND_URL;
const API_URL = process.env.NEXT_PUBLIC_API_URL;

// Axios instance for client-side requests
export const api = axios.create({
  baseURL: API_URL,
});

// Add auth token to requests
if (typeof window !== 'undefined') {
  const token = localStorage.getItem('token');
  if (token) {
    api.defaults.headers.common['Authorization'] = `Bearer ${token}`;
  }
}

// Server-side fetch functions with caching
export async function fetchJobs(params: Record<string, any> = {}) {
  try {
    const queryParams = new URLSearchParams(params).toString();
    const url = `${API_URL}/jobs${queryParams ? `?${queryParams}` : ''}`;
    
    const res = await fetch(url, {
      next: { revalidate: 3600 } // Revalidate every hour
    });
    
    if (!res.ok) return [];
    return res.json();
  } catch (error) {
    console.error('Failed to fetch jobs:', error);
    return [];
  }
}

export async function fetchJob(slug: string) {
  const res = await fetch(`${API_URL}/jobs/${slug}`, {
    next: { revalidate: 3600 }
  });
  
  if (!res.ok) return null;
  return res.json();
}

export async function fetchJobsByCategory(category: string, limit: number = 20) {
  try {
    const res = await fetch(`${API_URL}/jobs?category=${category}&limit=${limit}`, {
      next: { revalidate: 3600 }
    });
    
    if (!res.ok) return { jobs: [], total: 0 };
    const jobs = await res.json();
    
    // Return in expected format
    return {
      jobs: Array.isArray(jobs) ? jobs : [],
      total: Array.isArray(jobs) ? jobs.length : 0
    };
  } catch (error) {
    console.error('Failed to fetch jobs by category:', error);
    return { jobs: [], total: 0 };
  }
}

export async function getJobCount(category: string) {
  try {
    // First try to get count from jobs endpoint
    const res = await fetch(`${API_URL}/jobs?category=${category}`, {
      next: { revalidate: 3600 }
    });
    
    if (!res.ok) return 0;
    const jobs = await res.json();
    return Array.isArray(jobs) ? jobs.length : 0;
  } catch (error) {
    return 0;
  }
}

export async function fetchBlogs() {
  const res = await fetch(`${API_URL}/blogs`, {
    next: { revalidate: 3600 }
  });
  
  if (!res.ok) throw new Error('Failed to fetch blogs');
  return res.json();
}

export async function fetchBlog(slug: string) {
  const res = await fetch(`${API_URL}/blogs/${slug}`, {
    next: { revalidate: 3600 }
  });
  
  if (!res.ok) return null;
  return res.json();
}

// Client-side authenticated requests
export async function applyToJob(jobId: string, applicationData: any) {
  return api.post(`/jobs/${jobId}/apply`, applicationData);
}

export async function saveJob(jobId: string) {
  return api.post(`/jobs/${jobId}/save`);
}

export async function login(email: string, password: string) {
  const response = await api.post('/auth/login', { email, password });
  const { access_token } = response.data;
  
  if (typeof window !== 'undefined') {
    localStorage.setItem('token', access_token);
    api.defaults.headers.common['Authorization'] = `Bearer ${access_token}`;
  }
  
  return response.data;
}

export async function register(userData: any) {
  return api.post('/auth/register', userData);
}

export async function fetchCurrentUser() {
  return api.get('/auth/me');
}

export async function logout() {
  if (typeof window !== 'undefined') {
    localStorage.removeItem('token');
    delete api.defaults.headers.common['Authorization'];
  }
}
